import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AdminComponent } from './theme/layout/admin/admin.component';
import {AuthComponent} from './theme/layout/auth/auth.component';

const routes: Routes = [
  {
    path: '',
    component: AdminComponent,
    children: [
      {
        path: '',
        redirectTo: 'dashboard/analytics',
        pathMatch: 'full'
      },
      {
        path: 'dashboard',
        loadChildren: () => import('./demo/dashboard/dashboard.module').then(module => module.DashboardModule)
      },
      {
        path: 'layout',
        loadChildren: () => import('./demo/pages/layout/layout.module').then(module => module.LayoutModule)
      },
      {
        path: 'basic',
        loadChildren: () => import('./demo/ui-elements/ui-basic/ui-basic.module').then(module => module.UiBasicModule)
      },
      {
        path: 'forms',
        loadChildren: () => import('./demo/pages/form-elements/form-elements.module').then(module => module.FormElementsModule)
      },
      {
        path: 'tbl-bootstrap',
        loadChildren: () => import('./demo/pages/tables/tbl-bootstrap/tbl-bootstrap.module').then(module => module.TblBootstrapModule)
      },
      {
        path: 'charts',
        loadChildren: () => import('./demo/pages/core-chart/core-chart.module').then(module => module.CoreChartModule)
      },
      {
        path: 'sample-page',
        loadChildren: () => import('./demo/pages/sample-page/sample-page.module').then(module => module.SamplePageModule)
      },
      {
        path: 'supply-request',
        loadChildren: () => import('./demo/pages/supply-request/supply-request.module').then(module => module.SupplyRequestPageModule)
      },
      {
        path: 'procurement',
        loadChildren: () => import('./demo/pages/procurement/procurement.module').then(module => module.ProcurementPageModule)
      },
      {
        path: 'stock',
        loadChildren: () => import('./demo/pages/stock/stock.module').then(module => module.StockPageModule)
      },
      {
        path: 'item-type/:type',
        loadChildren: () => import('./demo/pages/stock/stock.module').then(module => module.StockPageModule)
      },
      {
        path: 'member',
        loadChildren: () => import('./demo/pages/member/member.module').then(module => module.MemberPageModule)
      },
      {
        path: 'member/:pekerjaan',
        loadChildren: () => import('./demo/pages/member/member.module').then(module => module.MemberPageModule)
      },
      {
        path: 'profile',
        loadChildren: () => import('./demo/pages/profile/profile.module').then(module => module.ProfilePageModule)
      }
    ]
  },
  {
    path: '',
    component: AuthComponent,
    children: [
      {
        path: 'auth',
        loadChildren: () => import('./demo/pages/authentication/authentication.module').then(module => module.AuthenticationModule)
      },
      {
        path: 'maintenance',
        loadChildren: () => import('./demo/pages/maintenance/maintenance.module').then(module => module.MaintenanceModule)
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
